<template>
	<view class="box">
		<view class="ava-box">
			<view class="left">
				<view class="avatar-box">
					<view class="avatar" v-if="!user.avatarUrl"></view>
					<image lazy-load class="avatar" v-else :src="user.avatarUrl" mode="aspectFill"></image>
					<image lazy-load class="wechat" v-if="user.loginType=='WX'" src="/static/images/wechat.png" mode="aspectFill"></image>
				</view>
				<view class="name" v-if="user.memberId">
					{{user.nickname?user.nickname:"未设置用户名"}}
					<view class="id" v-if="user.memberId">
						ID:{{user.memberId}}
					</view>
				</view>
				<view class="name" v-if="!user.memberId">
					<navigator url="/pages/login/login" class="id">
						未登陆
					</navigator>
				</view>
				
			</view>
			<navigator url="/pages/user/set" class="right crown">
				设置
			</navigator>
			<navigator url="/pages/member/open_vip" class="vip-box">
				<view class="left">
					<view class="title font-bold">
						嗨省会员,专享特权
					</view>
					<view class="price">
						预计为你节约￥6984/年
					</view>
				</view>
				<view class="right crown">
					立即开通
				</view>
			</navigator>
		</view>
		<view class="order-list">
			<view class="order-title">
				我的订单
				<navigator url='/pages/member/order' class="right">查看全部
					<view class="iconfont iconxiayibu"></view>
				</navigator>
			</view>
			<view class="order-box">
				<navigator url='/pages/member/order?status=0' class="order-li">
					<image lazy-load class="icon-img" src="/static/images/all_order.png" mode="aspectFill"></image>
					<view class="text">全部</view>
				</navigator>
				<navigator url='/pages/member/order?status=1' class="order-li">
					<image lazy-load class="icon-img" src="/static/images/no_pay.png" mode="aspectFill"></image>
					<view class="text">待付款</view>
				</navigator>
				<navigator url='/pages/member/order?status=2' class="order-li">
					<image lazy-load class="icon-img" src="/static/images/no_reseve.png" mode="aspectFill"></image>
					<view class="text">处理中</view>
				</navigator>
				<navigator url='/pages/member/order?status=3' class="order-li">
					<image lazy-load class="icon-img" src="/static/images/no_send.png" mode="aspectFill"></image>
					<view class="text">已完成</view>
				</navigator>
				<navigator url='/pages/member/order?status=4' class="order-li">
					<image lazy-load class="icon-img" src="/static/images/jishou.png" mode="aspectFill"></image>
					<view class="text">寄售</view>
				</navigator>
			</view>
		</view>
		<view class="order-list money-list">
			<view class="order-title">
				我的金库
			</view>
			<view class="money-box">
				<view class="money-li">
					<view class="num">{{goldBeanValue | formatNum}}</view>
					<view class="title">金豆</view>
				</view>
				<view class="money-li">
					<view class="num">{{merchantCashValue | formatNum}}</view>
					<view class="title">可提现金额</view>
				</view>
				<view class="money-li">
					<view class="num">{{couponValue | formatNum}}</view>
					<view class="title">团批劵</view>
				</view>
			</view>
		</view>
		<navigator url="/pages/index/area_new_people">
			<image lazy-load class="member-free" src="/static/images/member-free.png" mode="widthFix"></image>
		</navigator>
		<view class="car-tab" v-if="type_check">
			<template v-for="(item,k) in change">
				<navigator :url="item.url" :key="k" class="car-li" v-if="item.title!='我的客服'">
					<view class="icon-box">
						<view class="iconfont" :class="item.icon">
						</view>
						<view class="new" v-if='item.tips'>
							{{item.tips}}
						</view>
					</view>
					<view class="text">
						{{item.title}}
					</view>
				</navigator>
				<button open-type="contact" class="car-li no-btn" v-if="item.title=='我的客服'">
					<view class="icon-box">
						<view class="iconfont" :class="item.icon">
						</view>
						<view class="new" v-if='item.tips'>
							{{item.tips}}
						</view>
					</view>
					<view class="text">
						{{item.title}}
					</view>
				</button>
			</template>
		</view>
		<view class="car-tab" v-if="!type_check">
			<template v-for="(item,k) in check_list">
				<navigator :url="item.url" :key="k" class="car-li" v-if="item.title!='我的客服'">
					<view class="icon-box">
						<view class="iconfont" :class="item.icon">
						</view>
						<view class="new" v-if='item.tips'>
							{{item.tips}}
						</view>
					</view>
					<view class="text">
						{{item.title}}
					</view>
				</navigator>
				<button type="default" v-if="item.title=='我的客服'">
					<view class="icon-box">
						<view class="iconfont" :class="item.icon">
						</view>
						<view class="new" v-if='item.tips'>
							{{item.tips}}
						</view>
					</view>
					<view class="text">
						{{item.title}}
					</view>
				</button>
			</template>
		</view>

		<view class="car-tab adver-box" v-if="type_check">
			<view class="big-title">
				广告推广
			</view>
			<navigator url="/pages/member/article" class="adver-li">
				<view class="left">
					<view class="name">
						我的文章
					</view>
					<view class="tips">
						文章推广看这里>
					</view>
				</view>
				<view class="iconfont iconwodewenzhang">
				</view>
			</navigator>
			<view class="adver-li"  v-if="type_check">
				<view class="left">
					<view class="name">
						我的名片
					</view>
					<view class="tips">
						电子名片更方便>
					</view>
				</view>
				<view class="iconfont iconwodekapian">

				</view>
			</view>
			<navigator url="/pages/member/share_money" class="adver-li"  v-if="type_check">
				<view class="left">
					<view class="name">
						分享赚钱
					</view>
					<view class="tips">
						分享送超大福利>
					</view>
				</view>
				<view class="iconfont iconfenxiangzhuanqian">
				</view>
			</navigator>
			<navigator url="/pages/member/apply_agent" class="adver-li"  v-if="type_check">
				<view class="left">
					<view class="name">
						区域代理
					</view>
					<view class="tips">
						开启赚钱之旅>
					</view>
				</view>
				<view class="iconfont iconquyudaili">

				</view>
			</navigator>
		</view>
	</view>

</template>

<script>
	import {
		mapState
	} from 'vuex';
	export default ({
		data() {
			return {
				customerSeqId: "",
				customerName: "",
				customerNickName: "",
				user: {
					avatarUrl: "",
					nickname: "",
					userId: "",
					memberId: "",
					loginType: "MOBILE",
				},
				goldBeanValue: 0, //金豆余额
				merchantCashValue: 0, //活动积分
				couponValue: 0, //团批劵
				check_list: [{
						url: '/pages/member/address_list',
						icon: 'icondizhiguanli',
						title: "地址管理",
						tips: ""
					},
					{
						url: '/pages/goods/shop_car',
						icon: 'icongouwuche1',
						title: "购物车",
						tips: ""
					},
					{
						url: '/pages/member/integral',
						icon: 'iconwodejifen',
						title: "我的积分",
						tips: "抢"
					},
					{
						url: '/pages/member/activity_card',
						icon: 'iconwodekapian',
						title: "我的卡片",
						tips: ""
					}
				],
				list: [{
						url: '/pages/member/my_team',
						icon: 'iconwodetuandui',
						title: "我的团队",
						tips: ""
					},
					{
						url: '/pages/member/address_list',
						icon: 'icondizhiguanli',
						title: "地址管理",
						tips: ""
					},
					{
						url: '/pages/people/sd',
						icon: 'iconshangpinguanli',
						title: "商品管理",
						tips: ""
					},
					{
						url: '/pages/index/wait',
						icon: 'iconfenxiangtuiguang',
						title: "分享海报",
						tips: ""
					},
					{
						url: '/pages/member/integral',
						icon: 'iconwodejifen',
						title: "我的积分",
						tips: "抢"
					},
					{
						url: '/pages/member/activity_card',
						icon: 'iconwodekapian',
						title: "我的卡片",
						tips: ""
					},
					{
						url: '/pages/index/wait',
						icon: 'icongerenjiankang',
						title: "个人健康",
						tips: ""
					},
					{
						url: '/pages/member/invite',
						icon: 'iconyaoqinghaoyou',
						title: "邀请商家",
						tips: "热"
					},
					{
						url: '/pages/people/index',
						icon: 'iconshangjiaruzhu',
						title: "%E5%95%86%E5%AE%B6%E5%85%A5%E9%A9%BB",
						tips: "新"
					},
					{
						url: '/pages/member/identity',
						icon: 'iconshenfenguanli',
						title: "身份管理",
						tips: ""
					},
					{
						url: '/pages/index/wait',
						icon: 'iconwodekefu',
						title: "我的客服",
						tips: ""
					},
					{
						url: '/pages/index/wait',
						icon: 'iconshujuzhongxin1',
						title: "数据中心",
						tips: ""
					}
				]
			}
		},
		computed: {
			change() {
				for (let item of this.list) {
					item.title = decodeURIComponent(item.title)
				}
				return this.list
			},
			...mapState({
				type_check: state => state.type_check,
				user_state: state => state.user,
				account: state => state.account
			})

		},
		methods: {
			getData() {
				let user_json = this.user_state;
				this.user = {
					avatarUrl: user_json.avatarUrl || "",
					nickname: user_json.nickname || "",
					userId: user_json.userId || "",
					memberId: user_json.memberId || "",
					loginType: user_json.loginType || "MOBILE",
				}
				this.goldBeanValue = this.account.goldBeanValue || 0; //金豆余额
				this.merchantCashValue = this.account.merchantCashValue || 0; //可提现金额
				this.couponValue = this.account.couponValue || 0; //团批劵
				this.getCheck()
				this.getMoney()
			},
			getMoney() {
				this.$api.get('/o2oCustomerCash/getAccountBalanceBySheetId', {
					params: {
						customerSeqId: this.$common.getLocalSync('customerSeqId')
					}
				}).then(res => {
					let data = res.data;
					if (res.code == 200) {
						this.merchantCashValue = data.merchantCashValue; //可提现金额
						this.couponValue = data.couponValue; //团批劵
						this.goldBeanValue = data.goldBeanValue; //金豆余额
						let merchantSeqId = data.merchantSeqId; //商户id
						this.$common.setLocal('merchantSeqId', merchantSeqId)
						this.$store.commit('saveAccount', {
							merchantCashValue: this.merchantCashValue,
							couponValue: this.couponValue,
							goldBeanValue: this.goldBeanValue
						})
					}
				})
			},
			getCheck() {
				this.$api.get('/O2oMenuConfig/selectO2oMenu').then(res => {
					if (res.code == 200) {
						let type_check = res.data.menu == 1 ? true : false;
						this.$store.commit('saveTypecheck', type_check)
					}
				})
			}

		}
	})
</script>

<style lang="scss">
	// 我的金库
	.money-list {
		margin: 30rpx auto !important;

		.money-box {
			display: flex;
			justify-content: space-between;

