					width: 100%;
					font-size: 36rpx;
					color: #D14036;
					margin: 30rpx 0;
				}

				.title {
					font-size: 24rpx;

				}

				&::after {
					position: absolute;
					top: 50%;
					margin-top: -22rpx;
					right: 0;
					z-index: 10;
					content: "";
					width: 1px;
					height: 44rpx;
					background: #C6C6C6;

				}
			}

		}
	}

	.money-li:last-child::after {
		background: white !important;
	}

