			justify-content: space-between;
			font-size: 22rpx;
			color: #933E38;
			flex-shrink: 0;
			padding: 25rpx;
			padding-bottom: 15rpx;
			align-items: center;
			margin-bottom: 10rpx;

			.left {
				flex-grow: 1;
				text-align: left;

				.name {
					font-size: 32rpx;
					color: #702C27;
					margin-bottom: 16rpx;
				}
			}

			// #ifndef h5
			.iconfont {
				width: 90rpx;
				height: 90rpx;
				background: rgba(255, 255, 255, 1);
				border-radius: 50%;
				color: #DE797B;
				font-size: 50rpx;
				flex-shrink: 0;
				text-align: center;
				line-height: 90rpx;
			}

			// #endif
			// #ifdef h5
			.iconfont {
				width: 70rpx;
				height: 70rpx;
				background: rgba(255, 255, 255, 1);
				border-radius: 50%;
				color: #DE797B;
				font-size: 40rpx;
				flex-shrink: 0;
				text-align: center;
				line-height: 70rpx;
			}

			// #endif
		}
	}


	.ava-box {
		background: url('~@/static/images/member-bg.png') no-repeat;
		background-position: center;
		background-size: 100% 100%;
		width: calc(100% - 68rpx);
